@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;700&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0;
  vertical-align: top;
}

html,
body {
  width: 100%;
  height: 100%;
}

html {
  overflow-y: scroll;
}

header,
main,
footer,
nav,
aside,
section {
  display: block;
}

ul {
  list-style: none;
}

a,
a:hover {
  text-decoration: none;
}

button {
  display: block;
}

input:focus {
  outline: none;
}

body {
  background: #222;
}

body * {
  font-family: "Noto Sans KR", sans-serif;
  color: #222;
}

.container {
  max-width: 600px;
  margin: 0 auto;
  padding: 30px 0;
}

section {
  padding: 30px;
  background: #fff;
  border-radius: 16px;
}

section + section {
  margin-top: 50px;
}

section h2 {
  margin-bottom: 20px;
  font-size: 24px;
}

.sectionUnit select {
  padding: 8px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 15px;
}

.sectionUnit .inputWrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.sectionUnit .inputWrap .equal {
  margin-right: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.sectionUnit .inputEach {
  width: 30%;
  margin-right: 10px;
}

.sectionUnit .inputEach label {
  display: block;
  margin-bottom: 5px;
  font-size: 15px;
  font-weight: bold;
}

.sectionUnit .inputEach input {
  width: 100%;
  padding: 8px 12px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 15px;
  line-height: 22px;
}

.sectionUnit .inputEach input[disabled] {
  background: #eee;
}

.sectionUnit .buttonControl {
  padding: 8px 16px;
  background: #4692ff;
  border: 1px solid #4692ff;
  border-radius: 8px;
  text-transform: uppercase;
  font-size: 15px;
  line-height: 22px;
  color: white;
  cursor: pointer;
  width: 100px;
  margin-top: auto;
  font-weight: bold;
}

.sectionUnit .buttonControl + .buttonControl {
  margin-left: 10px;
  background: #fff;
  color: #4692ff;
}

.sectionTodo form {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 15px;
}

.sectionTodo input {
  width: 100%;
  padding: 8px 12px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 15px;
}

.sectionTodo button {
  padding: 8px 16px;
  background: #4692ff;
  border: 1px solid #4692ff;
  border-radius: 8px;
  text-transform: uppercase;
  font-size: 15px;
  line-height: 22px;
  color: white;
  cursor: pointer;
  width: 100px;
  font-weight: bold;
}

.sectionTodo ul {
  margin: 20px 0 0 4px;
}

.sectionTodo li {
  margin-top: 8px;
}

.sectionTodo li::before {
  content: "";
  display: inline-block;
  width: 4px;
  height: 4px;
  margin-right: 8px;
  border-radius: 2px;
  background: #000;
  vertical-align: middle;
}
